import React from 'react';
import { nanoid } from 'nanoid'
import { firebase } from '../../providers/firebase';
//import "bootstrap/dist/css/bootstrap.min.css"
function Task() {
  const [state, setState] = React.useState({
    "task": "",
    "tasks": [],
    "editionMode": false,
    "id": "",
    "error": null,
  });

  React.useEffect(() => {
    getTasksFire();

  }, [])
  const getTasksFire = async () => {
    try {
      let db = firebase.firestore();
      const data = await db.collection('task_udemy').get();
      const arrayData = data.docs.map(doc => ({ id: doc.id, ...doc.data() }))
      console.log(data.docs);
      console.log(arrayData);
      setState({ ...state, tasks: arrayData, task:"" });
    } catch (error) {
      console.log(error);

    }


  };

  const handdleChange = (e) => {

    setState({ ...state, task: e.target.value })

  };

  const onUpdate = (e) => {
    e.preventDefault();

    if (state.task.trim()) {
      let items = state.tasks.map(item => item.id === state.id ? { id: state.id, name: state.task } : item);
      setState({ ...state, tasks: items, error: null });

    } else {
      setState({ ...state, error: "Debe ingresar una tarea" })
    }
  };

  const onUpdateFire= async(e)=>{
    e.preventDefault();
    if(!state.task.trim()){
      setState({ ...state, error: "Debe ingresar una tarea" })
      return;
    }
    let db = firebase.firestore();
    await db.collection('task_udemy').doc(state.id).update({
      name:state.task
    });
    setState({ ...state, error: null });
    getTasksFire();
  }  


  const onSubmit = (e) => {
    e.preventDefault();

    if (state.task.trim()) {
      let items = state.tasks;
      let item = {};
      item = {
        "id": nanoid(),
        "name": state.task
      };
      items.push(item);
      setState({ ...state, tasks: items, task: "", error: null });
      console.log(state.tasks);

    } else {
      setState({ ...state, error: "Debe ingresar una tarea" })
    }
  };

  const onSaveFirebase= async(e)=>{
    try{
      e.preventDefault();

      if (state.task.trim()) {
        
        let item = {};
        item = {
          "name": state.task,
          "date":Date.now()
        };
        const db = firebase.firestore()
        const data = await db.collection('task_udemy').add(item)
        getTasksFire();
        setState({ ...state, task: "", error: null });
  
      } else {
        setState({ ...state, error: "Debe ingresar una tarea" })
      }

    }catch(error){
      console.log(error)

    }
    


  };

  const onDelete = (index) => {
    let items = state.tasks;
    items.splice(index, 1);
    setState({ ...state, tasks: items });

  };

  const onDeleteFire = async(id)=>{
    try{
      const db = firebase.firestore()
      await db.collection('task_udemy').doc(id).delete()
      getTasksFire();

    }catch(error){
      console.log(error)

    };
  };


  /*
    const eliminarTarea = id => {
      const arrayFiltrado = tareas.filter(item => item.id !== id)
      setTareas(arrayFiltrado)
    }*/

  const onEdit = (task) => {

    setState({ ...state, editionMode: true, task: task.name, id: task.id });

  };
  const cancelEdition = () => {

    setState({ ...state, editionMode: false, task: "", id: "" });

  };

  return (

    <div className="container mt-5" >
      <h1 className="text-center">Proyecto CRUD Tareas</h1>
      <hr />
      <div className="row">
        <div className="col-8">
          <h4 className="text-center">Lista de tareas</h4>
          <ul className="list-group">
            {state.tasks.length > 0 ? (
              state.tasks.map((task, index) => (
                <li className="list-group-item" key={task.id}>
                  <span className="lead">{task.name}  </span>

                  <button className="btn btn-danger bnt-sm float-right"
                    onClick={() => onDeleteFire(task.id)}
                  >
                    Eliminar
                  </button>
                  <button className="btn btn-warning bnt-sm float-right mx-2"
                    onClick={() => onEdit(task)}
                  >
                    Modificar
                  </button>
                </li>
              ))
            ) : (
              <li className="list-group-item">No hay tareas</li>

            )}


          </ul>

        </div>
        <div className="col-4">

          <h4 className="text-center">
            {
              state.editionMode ? (
                <div className="row">
                  Modificar Tarea
                  <button className="btn btn-danger bnt-sm float-right"
                    onClick={cancelEdition}  > x </button>
                </div>




              ) : (
                "Agregar Tarea"

              )
            }
          </h4>
          <form onSubmit={state.editionMode ? onUpdateFire : onSaveFirebase}>
            {
              state.error ? <span className="text-danger">{state.error}</span> : null
            }
            <input type="text"
              placeholder="Ingrese tarea"
              className="form-control mb-2"
              value={state.task}
              name="task"
              onChange={handdleChange}
            />

            {
              state.editionMode ? (
                <button type="submit" className="btn btn-primary btn-block">
                  Actualizar
                </button>
              ) : (
                <button type="submit" className="btn btn-dark btn-block">
                  Agregar
                </button>
              )
            }


          </form>

        </div>
      </div>

    </div>

  );
}

export default Task;
