import React, { useEffect } from 'react';
import { useParams } from 'react-router';


function Civilizations(params) {
    
    const {id} = useParams();
    const [state, setState]= React.useState({
        'civilization':null
    });
    useEffect(() => {
        getCivilization();
       
    }, [])

    const getCivilization=async()=>{
        const res = await fetch(`https://age-of-empires-2-api.herokuapp.com/api/v1/civilization/${id}`);
        const data = await res.json();
        console.log(id);
        setState({...state,civilization:data});

    };


    return(
        <div className="container col-12 mt-2">
            {state.civilization?(
                <>
                <h3 className="text-center">
            {state.civilization?(state.civilization.name):("Undefined Civilization")} </h3>
            <span>
                {state.civilization?(state.civilization.expansion):("Undefined Civilization")}
            </span>
            <div className="row">
                <div className="container col-7">
                    <h5 className="text-center">General information</h5>
                    
                    <div className="row">
                        <p className="font-weight-bold mr-2">Army type : </p>
                        <div className="span">{state.civilization.army_type}</div>
                    </div>
                    <div className="row">
                        <p className="font-weight-bold mr-2">Team bonus : </p>
                        <div className="span">{state.civilization.team_bonus}</div>
                    </div>

                    <div className="row">
                        <p className="font-weight-bold mr-2">Civilization bonus : </p> <br />
                        {state.civilization.civilization_bonus&&state.civilization.civilization_bonus.length>0?(
                            state.civilization.civilization_bonus.map((bonus, index)=>(
                                <li key={index}>{bonus}</li>

                            ))

                        ):(
                            "No Civilization Bonus"

                        )}
                        
                    </div>

                    
                
                </div>
                <div className="container col-3">


                </div>

                
            </div>
                </>

            ):(
                <span className="text-center">
                    No Data
                </span>
            )}
            

        </div>
    );
}

export default Civilizations;