import React from 'react';
import {
    Link
  } from "react-router-dom";

function AgeEmpires() {

    const [state, setState] = React.useState({
        'civilizations': null,
    });


    React.useEffect(() => {
        getCivilizations();

    }, []);

    const getCivilizations = async () => {

        //https://age-of-empires-2-api.herokuapp.com/api/v1/civilizations
        const data = await fetch('https://age-of-empires-2-api.herokuapp.com/api/v1/civilizations')
        let dataJson = await data.json();
        setState({ ...state, civilizations: dataJson.civilizations });
        
    };

    return (
        <div className="container mt-3">
            <h1 className="text-center">
                Age Of Empires
            </h1>
            {
                <div className="row">
                    <div className="container col-8">
                        <h3 className="text-center">
                            Civilizations
                        </h3>
                        <hr />

                        <ul className="list-group">
                            {state.civilizations ? (
                                state.civilizations.map((civilization) => (
                                    <li className="list-group-item" key={civilization.id}>
                                        <div className="row justify-content-center">
                                            <div className="container col-9">
                                                <span>{civilization.name}</span>
                                                <hr />
                                                <span>{civilization.expansion}</span>
                                            </div>
                                            <div className="container col-3 justify-content-center">
                                                <div className="row h-100 justify-content-center align-items-center">
                                                    <Link to={`age-empires/${civilization.id}`} className="btn btn-outline-info ">
                                                        Detalle
                                                    </Link>
                                                </div>

                                            </div>
                                        </div>




                                    </li>

                                ))


                            ) : (
                                <></>
                            )}
                        </ul>

                    </div>
                    <container className="col-4">

                    </container>
                </div>


            }


        </div>

    )

}

export default AgeEmpires;